module.exports = "div.container {\n  display: flex;\n  justify-content: space-around;\n  margin-top: 20px;\n  margin-bottom: 20px;\n}\n\ndiv.outer {\n  border: 1px solid black;\n  padding: 10px;\n  margin: 20px;\n}\n\ndiv.list {\n  border: 1px solid black;\n  min-width: 100px;\n  padding: 10px;\n}\n\n.draggable {\n  /* display: block; */\n  max-width: -webkit-fit-content;\n  max-width: -moz-fit-content;\n  max-width: fit-content;\n  border: 1px solid black;\n  background-color: white;\n  padding: 5px;\n}\n\nspan > pre.draggable {\n  margin: 5px;\n}\n\ndiv.cdk-drop-list {\n  border: 1px solid blue;\n}\n\nspan.cdk-drop-list {\n  border: 1px solid blue;\n  color: blue;\n}\n\ndiv.cdk-drop-list > pre:first-child {\n  color: blue;\n}\n\n.cdk-drag {\n  border: 1px solid red;\n  color: red;\n}\n\n.cdk-drag-preview {\n  background-color: pink;\n  border: 1px solid black;\n  color: black;\n}\n\n.cdk-drag-placeholder {\n  background-color: yellow;\n  border: 1px solid black;\n  color: black;\n}\n\n.cdk-drop-list-dragging {\n  background-color: lightcyan;\n  border: 1px solid black;\n  color: black;\n}\n\n.cdk-drop-list-receiving {\n  background-color: lightgreen;\n  border: 1px solid black;\n  color: black;\n}\n\n/* Animate items as they're being sorted. */\n\n.cdk-drop-list-dragging .cdk-drag {\n  transition: -webkit-transform 250ms cubic-bezier(0, 0, 0.2, 1);\n  transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);\n  transition: transform 250ms cubic-bezier(0, 0, 0.2, 1), -webkit-transform 250ms cubic-bezier(0, 0, 0.2, 1);\n}\n\n/* Animate an item that has been dropped. */\n\n.cdk-drag-animating {\n  transition: -webkit-transform 300ms cubic-bezier(0, 0, 0.2, 1);\n  transition: transform 300ms cubic-bezier(0, 0, 0.2, 1);\n  transition: transform 300ms cubic-bezier(0, 0, 0.2, 1), -webkit-transform 300ms cubic-bezier(0, 0, 0.2, 1);\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"